









































import { Component, Vue } from "vue-property-decorator";
import { GlobalLessonsService } from "@/services/lessonsService";
import { EventType, GlobalEvents } from "@/services/eventSystem";
import { aspectsName } from "@/data/data";

@Component
export default class TestTable extends Vue {
  private aspects: number[] = [];
  private dataForView: SimpleObject = {};
  private maxLength = 0;

  mounted() {
    this.loadData();
    GlobalEvents.subscribe(EventType.LESSONS_LOAD, this.loadData);
  }

  beforeDestroy() {
    GlobalEvents.unsubscribe(EventType.LESSONS_LOAD, this.loadData);
  }

  private get aspectsView() {
    return this.aspects || [];
  }

  private loadData() {
    let maxLength = 0;
    const aspects: number[] = [];
    const newData: SimpleObject = {};
    const data = GlobalLessonsService.tests;
    for (const item of data) {
      const aspect = item.aspect;
      if (!Object.prototype.hasOwnProperty.call(newData, aspect)) {
        newData[aspect] = [];
        aspects.push(aspect);
      }
      newData[aspect].push(item.score);
    }
    for (let j = 0; j < aspects.length; j++)
      maxLength = Math.max(maxLength, newData[aspects[j]].length);
    newData.total = [];
    for (let i = 0; i < maxLength; i++) {
      let cnt = 0;
      let total = 0;
      for (let j = 0; j < aspects.length; j++) {
        if (newData[aspects[j]]) {
          cnt++;
          total += newData[aspects[j]][i];
        }
      }
      if (cnt > 0) total = total / cnt;
      newData.total.push(total);
    }
    this.maxLength = maxLength;
    this.aspects = aspects;
    this.dataForView = newData;
  }

  protected getNameForAspect(id: number) {
    return Object.prototype.hasOwnProperty.call(aspectsName, id)
      ? aspectsName[id]
      : "";
  }

  protected formatValue(value: number) {
    return value.toFixed(1);
  }
}
