import {
  fetchWithTime,
  validateJson,
  validateStatus
} from "@/helpers/fetch-helpers";
import { EventType, GlobalEvents } from "@/services/eventSystem";

class LessonsService {
  private _marks: SimpleObject[] = [];
  private _tests: SimpleObject[] = [];

  constructor() {
    this.loadData();
  }

  public get marks() {
    return this._marks;
  }

  public get tests() {
    return this._tests;
  }

  private loadData() {
    // https://my.hotspot.school/a/lessons
    fetchWithTime("https://oruman.github.io/hsea/json/lessons.json")
      .then(validateStatus)
      .then(response => response.json())
      .then(validateJson)
      .then(response => {
        const marks = [];
        const tests = [];
        for (const item of response as SimpleObject[]) {
          const dt = parseInt(item.date, 10) / 1000;
          const tmp = Object.values(item.marks);
          marks.push({
            date: dt,
            aspect: item.aspect,
            mark: tmp.length > 0 ? tmp[0] : ""
          });
          if (!item.is_test) continue;
          const tmp2 = Object.values(item.test_marks);
          tests.push({
            date: dt,
            aspect: item.aspect,
            score: tmp2.length > 0 ? tmp2[0] : 0
          });
        }
        marks.sort((a, b) => a.date - b.date);
        tests.sort((a, b) => a.date - b.date);
        this._marks = marks;
        this._tests = tests;
        GlobalEvents.publish(EventType.LESSONS_LOAD, null);
      });
  }
}

export const GlobalLessonsService = new LessonsService();
