























import { Component, Vue } from "vue-property-decorator";
import { GlobalFileService } from "@/services/fileService";
import Tabs from "@/components/Tabs.vue";
import { EventType, GlobalEvents } from "@/services/eventSystem";

@Component({
  components: { Tabs }
})
export default class Materials extends Vue {
  private activeTab = 101;
  private data: SimpleObject[] = [];

  mounted() {
    this.loadData();
    GlobalEvents.subscribe(EventType.FILES_LOAD, this.loadData);
  }

  beforeDestroy() {
    GlobalEvents.unsubscribe(EventType.FILES_LOAD, this.loadData);
  }

  private changeTab(tabId: number) {
    this.activeTab = tabId * 100 + 1;
  }

  private get materials() {
    return this.data.filter(item => item.fileKey == this.activeTab);
  }

  private loadData() {
    this.data = GlobalFileService.files;
  }
}
