


















































import { Component, Vue } from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
import { GlobalGradesService } from "@/services/gradesService";
import { EventType, GlobalEvents } from "@/services/eventSystem";
@Component({
  components: { Tabs }
})
export default class Curriculum extends Vue {
  private activeTab = 1;

  private data: SimpleObject[] = [];

  mounted() {
    this.loadData();
    GlobalEvents.subscribe(EventType.GRADES_LOAD, this.loadData);
  }

  beforeDestroy() {
    GlobalEvents.unsubscribe(EventType.GRADES_LOAD, this.loadData);
  }

  private get curriculum() {
    const newData = [];
    for (const items of this.data) {
      newData.push(
        items.filter((item: SimpleObject) => item.tabId == this.activeTab)
      );
    }
    return newData;
  }

  private changeTab(tabId: number) {
    this.activeTab = tabId;
  }

  private loadData() {
    const data: SimpleObject = GlobalGradesService.curriculum;
    if (!data) return;
    this.convertData(data);
  }

  private convertData(data: SimpleObject) {
    if (!data.header || !data.body) {
      this.data = [];
      return;
    }
    const headerData = [];
    for (const id in data.header) {
      if (!Object.prototype.hasOwnProperty.call(data.header, id)) continue;
      for (const tmp in data.header[id]) {
        if (!Object.prototype.hasOwnProperty.call(data.header[id], tmp))
          continue;
        headerData.push({
          tabId: id,
          name: data.header[id][tmp]
        });
      }
    }
    const newData = [];
    for (const weekNum in data.body) {
      if (!Object.prototype.hasOwnProperty.call(data.body, weekNum)) continue;
      const newWeek: SimpleObject[] = [];
      for (const itemId in data.body[weekNum]) {
        if (!Object.prototype.hasOwnProperty.call(data.body[weekNum], itemId))
          continue;
        const numId = parseInt(itemId, 10);
        if (isNaN(numId)) continue;
        const newItem: SimpleObject = {
          title: headerData[numId].name,
          tabId: headerData[numId].tabId
        };
        const items: SimpleObject[] = [];
        for (const task of data.body[weekNum][itemId]) {
          const newTask: SimpleObject = {
            text: task.title
          };
          if (task.link) newTask.link = task.link;
          items.push(newTask);
        }
        if (items.length) {
          newItem["items"] = items;
          newWeek.push(newItem);
        }
      }
      newData.push(newWeek);
    }
    this.data = newData;
  }
}
